import BottomMenuComponent from '../../components/BottomMenu/BottomMenuComponent';

class SettingsPage {
  constructor(authService, settingsService, settingsRotationComponent, bottomMenuComponent, showAlert) {
    this._authService = authService;
    this._settingsService = settingsService;
    this._settingsRotationComponent = settingsRotationComponent;
    this._bottomMenuComponent = bottomMenuComponent;
    this._showAlert = showAlert;

    // html elements
    this._pageElement = $('#settings-page');
    this._cookiesCheckBoxElement         = $('#settings-clear-cookies');
    this._cacheCheckBoxElement           = $('#settings-clear-cache');
    this._addNewExceptionButton          = $('#settings-cookies-add');
    this._removeExceptionsButton         = $('#settings-cookies-delete');
    this._newUrlExceptionField           = $('#settings-cookies-url');
    this._exceptionsList                 = $('#cookies-exception-list__box');

    // events
    this._cookiesCheckBoxElement.on('change', () => this.onChangeCookiesCheckBox() );
    this._cacheCheckBoxElement.on('change',   () => this.onChangeCacheCheckBox() );
    this._addNewExceptionButton.on('click',   () => this.onAddNewExceptionToCookiesClicked().then() );
    this._removeExceptionsButton.on('click',  () => this.onRemoveExceptions() );


    /* Settings */
    $('#settings-page > .header > div').on('click', function() {
      $('#settings-page > .header > div').removeClass('active');
      $(this).addClass('active');
      $('#settings-page > .content > div').hide();
      $('#settings-page > .content > div.' + $(this).attr('rel')).show();
    });

  }

  onChangeCookiesCheckBox() {
    let check = this._cookiesCheckBoxElement.prop('checked');
    if (check) {
      this._settingsService.enableClearCookies().then();
    }
    else {
      this._settingsService.disableClearCookies().then();
    }
  }

  onChangeCacheCheckBox() {
    let check = this._cacheCheckBoxElement.prop('checked');
    if (check) {
      this._settingsService.enableClearCache().then();
    }
    else {
      this._settingsService.disableClearCache().then();
    }
  }

  onRemoveExceptions() {
    let elements = this._exceptionsList.find('input[type="checkbox"]:checked');
    for (let i = 0; i < elements.length; i++) {
      const site = elements[i].value;
      this._settingsService.removeSiteFromExceptionList(site).then();
    }
    this.render();
  }

  async onAddNewExceptionToCookiesClicked() {
    let url = this._newUrlExceptionField.val();
    try {
      await this._addUrlToExceptionList(url);
      this._newUrlExceptionField.val('https://');
    }
    catch (e) {
        this._showAlert(e);
    }
  }

  show() {
    this._pageElement.show();
  }

  render() {
    this._bottomMenuComponent.showButtons([
      BottomMenuComponent.CANCEL_BUTTON,
      BottomMenuComponent.CONTACT_US_BUTTON,
    ]);

    this._settingsRotationComponent.render();

    if (this._authService.isNotAuthorized()) {
      this._renderForNotAuthorizedUser();
    }
    else {
      this._renderForAuthorizedUser();
    }

  }

  _renderForNotAuthorizedUser() {
    this._disableInputElements();
  }

  _renderForAuthorizedUser() {
    const clearCookiesValue = !!this._settingsService.getClearCookies();
    this._cookiesCheckBoxElement.prop('checked', clearCookiesValue);

    const clearCacheValue = !!this._settingsService.getClearCache();
    this._cacheCheckBoxElement.prop('checked', clearCacheValue);

    this._renderExceptionList();

    $('#settings-page .user-email').text(this._authService.getIdentity());
    this._enableInputElements();
  }

  _renderExceptionList() {
    let list = this._settingsService.getCookiesExceptionList();
    if (list) {
      $('#cookies-exception-list .default').hide();
      this._exceptionsList.empty();
      for (let i = 0; i < list.length; i++) {
        const url = list[i];
        const template = `
            <div style="white-space: nowrap; display: table">
                <input type="checkbox" id="exception_${i}" name="checkvalues" value="${url}">
                <label style="cursor: pointer; display: table-cell; vertical-align: middle" for="exception_${i}">${url}</label>
            </div>`;
        this._exceptionsList.append(template);
      }
    }
    else {
      $('#cookies-exception-list .default').show();
    }
  }

  async _addUrlToExceptionList(url) {
    await this._settingsService.appendCookiesExceptionList(url);
    this.render();
  }

  _disableInputElements() {
    this._cookiesCheckBoxElement.attr('disabled', 'disabled');
    this._cacheCheckBoxElement.attr('disabled', 'disabled');
    this._addNewExceptionButton.attr('disabled', 'disabled');
    this._removeExceptionsButton.attr('disabled', 'disabled');
    this._newUrlExceptionField.attr('disabled', 'disabled');

  }

  _enableInputElements() {
    this._cookiesCheckBoxElement.removeAttr('disabled');
    this._cacheCheckBoxElement.removeAttr('disabled');
    this._addNewExceptionButton.removeAttr('disabled');
    this._removeExceptionsButton.removeAttr('disabled');
    this._newUrlExceptionField.removeAttr('disabled');
  }
}

export default SettingsPage;
