/**
 * Component SettingsRotationInterval
 */
class SettingsRotationComponent {
  constructor(settingsService, authService, renderSystem) {
    this._settingsService = settingsService;
    this._authService = authService;
    this._renderSystem = renderSystem;
    this._el    = $('#settings-rotation-interval');
    this._input = this._el.find('input');

    this._input.on('change', () => this.onChange().then());
  }

  render() {
    if (this._settingsService.isCurrentHighRotation()) {
      this.setValue(0);
    }
    else if (this._settingsService.isCurrentRotationMinute()) {
      this.setValue(1);
    }
    else if (this._settingsService.isCurrentRotation10Minutes()) {
      this.setValue(2);
    }
    else if (this._settingsService.isCurrentRotation30Minutes()) {
      this.setValue(3);
    }

    if (this._authService.isNotAuthorized()) {
      this.disable();
    }
    else {
      this.enable();
    }
  }

  async onChange() {
    const map = [0, 60, 600, 1800];
    const seconds = map[this.getValue()];
    await this._settingsService.setRotation(seconds);
    this._renderSystem.render();
  }

  enable() {
    this._el.removeAttr('disabled');
    this._input.removeAttr('disabled');
  }

  disable() {
    this._el.attr('disabled', 'disabled');
    this._input.attr('disabled', 'disabled');
  }

  setValue(value) {
    this._input.val(value);
    this.trigger('input');
  }

  getValue() {
    return parseInt(this._input.val());
  }

  trigger(event) {
    this._input.trigger(event);
  }
}

export default SettingsRotationComponent;