/**
 * Component IpSwitch
 */
class SwitchIpComponent {
  constructor(locationService, loaderComponent, tabService) {
    this._locationService    = locationService;
    this._loaderComponent = loaderComponent;
    this._tabService      = tabService;

    this._button = $('#switch-ip');
    this._button.on('click', () => this.OnClickSwitchIP());
  }

  OnClickSwitchIP() {
    this._loaderComponent.show('Changing IP');
    this._locationService.changeIP()
    .then(
      () => {
        this._loaderComponent.hide();
        this._tabService.reloadCurrentTab().then();
      }
    );
  }

  render() {
    this.makeVisible();
    if (this._locationService.hasActivatedLocation() && this._locationService.getRotation() !== 0) {
      let location = this._locationService.getActivatedLocation();
      if (this._locationService.isRotationPossible(location)) {
        this.enable();
      }
      else {
        this.disable();
      }
    }
    else {
      this.disable();
    }

  }

  disable() {
    this._button.attr('disabled', 'disabled');
  }

  enable() {
    this._button.removeAttr('disabled');
  }

  makeInvisible() {
    this._button.addClass('invisible');
  }

  makeVisible() {
    this._button.removeClass('invisible');
  }
}

export default SwitchIpComponent;