/**
 * Component BottomMenu
 */
class BottomMenuComponent {
  constructor(locationCommonService, deviceService, navigator, authService, loaderComponent, renderSystem, tabService) {
    this._locationService = locationCommonService;
    this._deviceService   = deviceService;
    this._navigator       = navigator;
    this._authService     = authService;
    this._loaderComponent = loaderComponent;
    this._renderSystem    = renderSystem;
    this._tabService      = tabService;

    this._buttons = {};
    this._buttons[BottomMenuComponent.SETTENING_BUTTON]      = $('footer .button-settings');
    this._buttons[BottomMenuComponent.CONTACT_US_BUTTON]     = $('footer .button-contact-us');
    this._buttons[BottomMenuComponent.TURN_OFF_BUTTON]       = $('footer .button-turnoff');
    this._buttons[BottomMenuComponent.TURN_ON_BUTTON]        = $('footer .button-turnon');
    this._buttons[BottomMenuComponent.SUBMIT_BUTTON]         = $('footer .button-submit');
    this._buttons[BottomMenuComponent.CANCEL_BUTTON]         = $('footer .button-cancel');

    this._buttons[BottomMenuComponent.TURN_OFF_BUTTON].on('click',   () => this.onTurnOff());
    this._buttons[BottomMenuComponent.TURN_ON_BUTTON].on('click',    () => this.onTurnOn().then());
    this._buttons[BottomMenuComponent.CONTACT_US_BUTTON].on('click', () => this.onContactUs());
    this._buttons[BottomMenuComponent.CANCEL_BUTTON].on('click',     () => this.onCancel());
    this._buttons[BottomMenuComponent.SUBMIT_BUTTON].on('click',     () => this.onSubmit());
    this._buttons[BottomMenuComponent.SETTENING_BUTTON].on('click',  () => this.onSettings());
  }

  onSubmit() {
    function validate_email(email) {
      var reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
      return reg.test(email);
    }

    let $page = $('#contact-us-page');
    if (!$page.find('[name=subject]').val()) {
      $page.find('[name=subject]').focus();
      $('#contact-us-page-error')
      .css('visibility', 'visible')
      .text('Please insert subject');
      return;
    } else if (!$page.find('[name=email]').val()) {
      $page.find('[name=email]').focus();
      $('#contact-us-page-error')
      .css('visibility', 'visible')
      .text('Please insert contact email');
      return;
    } else if (!validate_email($page.find('[name=email]').val())) {
      $page.find('[name=email]').focus();
      $('#contact-us-page-error')
      .css('visibility', 'visible')
      .text('Please insert valid contact email');
      return;
    } else if (!$page.find('[name=department]').val()) {
      $page.find('[name=department]').focus();
      $('#contact-us-page-error')
      .css('visibility', 'visible')
      .text('Please choose department');
      return;
    } else if (!$page.find('[name=message]').val()) {
      $page.find('[name=message]').focus();
      $('#contact-us-page-error')
      .css('visibility', 'visible')
      .text('Please insert message.');
      return;
    }

    var params = {};
    params['user'] = $page.find('[name=email]').val();
    params['email'] = $page.find('[name=email]').val();
    params['subject'] = $page.find('[name=subject]').val();
    params['department'] = $page.find('[name=department]').val();
    params['msg'] = $page.find('[name=message]').val();

    $.ajax({
      url: 'http://toolbar.geosurf.com/toolbar/contact.php',
      type: 'POST',
      data: params,
      success: (data) => {
        this._navigator.goTo(this._navigator.ROUTES.THANKS_PAGE);
      },
      error: (data) => {
        $('#contact-us-page-error')
        .css('visibility', 'visible')
        .text('Send failed');
      }
    });
  }

  onContactUs() {
    this._navigator.goTo(this._navigator.ROUTES.CONTACT_US_PAGE);
  }

  onCancel() {
    const pageId  = this._authService.isAuthorized() ? this._navigator.ROUTES.MAIN_PAGE : this._navigator.ROUTES.LOGIN_PAGE;
    this._navigator.goTo(pageId);
    this._renderSystem.render();
  }

  onSettings() {
    this._navigator.goTo(this._navigator.ROUTES.SETTING_PAGE);
  }

  onTurnOff() {
    if (this._buttons[BottomMenuComponent.TURN_OFF_BUTTON].hasClass('disabled')) {
      return;
    }
    this._loaderComponent.show('Deactivate the current proxy');
    this._locationService.deactivateCurrentLocation()
    .then(
      () => {
        this._tabService.reloadCurrentTab().then();
        this._loaderComponent.hide();
        this._renderSystem.render();
      },
    );

  }

  async onTurnOn() {
    if (!(this._locationService.hasPrevLocations())) {
      return;
    }

    let prevHistoryLocation = this._locationService.getPrevLocation();
    this._locationService.changeLocationService(prevHistoryLocation.getAuthHandlerName());
    this._loaderComponent.show('Restoring the previous proxy');
    await this._locationService.activateLocation(prevHistoryLocation.getLocation());
    this._loaderComponent.hide();
    this._renderSystem.render();
    await this._tabService.reloadCurrentTab();
  }

  hideAll() {
    $('footer .button-footer').hide();
  }

  showButtons(buttons, hideAll) {
    if (hideAll !== false) {
      this.hideAll();
    }

    for (let i = 0; i < buttons.length; i++) {
      this.showButton(buttons[i]);
    }
  }

  showButton(buttonId) {
    this._buttons[buttonId].show();
  }

  showSettingsButton() {
    this.showButton(BottomMenuComponent.SETTENING_BUTTON);
  }

  showContactUsButton() {
    this.showButton(BottomMenuComponent.CONTACT_US_BUTTON);
  }

  showCancelButton() {
    this.showButton(BottomMenuComponent.CANCEL_BUTTON);
  }

  showTurnOffButton() {
    this.showButton(BottomMenuComponent.TURN_OFF_BUTTON);
  }

  hideTurnOffButton() {
    this._buttons[BottomMenuComponent.TURN_OFF_BUTTON].hide();
  }

  enableTurnOffButton() {
    this._buttons[BottomMenuComponent.TURN_OFF_BUTTON].addClass('active');
    this._buttons[BottomMenuComponent.TURN_OFF_BUTTON].removeClass('disabled');
  }

  disableTurnOffButton() {
    this._buttons[BottomMenuComponent.TURN_OFF_BUTTON].removeClass('active');
    this._buttons[BottomMenuComponent.TURN_OFF_BUTTON].addClass('disabled');
  }

  showTurnOnButton() {
    this.showButton(BottomMenuComponent.TURN_ON_BUTTON);
  }

  setTitleTurnOnButton(title) {
    this._buttons[BottomMenuComponent.TURN_ON_BUTTON].attr('title', title);
  }

  hideTurnOnButton() {
    this._buttons[BottomMenuComponent.TURN_ON_BUTTON].hide();
  }
}

BottomMenuComponent.SETTENING_BUTTON  = 'settings';
BottomMenuComponent.CONTACT_US_BUTTON = 'contactUs';
BottomMenuComponent.TURN_OFF_BUTTON   = 'turnOff';
BottomMenuComponent.TURN_ON_BUTTON    = 'turnOn';
BottomMenuComponent.SUBMIT_BUTTON     = 'submit';
BottomMenuComponent.CANCEL_BUTTON     = 'cancel';

export default BottomMenuComponent;