/**
 * Component DropDownMenu
 */
class DropDownMenuComponent {
  constructor(authService, navigator, countrySearchComponent) {
    this._authService = authService;
    this._navigator = navigator;
    this._countrySearchComponent = countrySearchComponent;

    this._button = $('header .button-menu');
    this._button.on('click', (event) => {
      event.stopPropagation();
      this.toggleMenu();
      countrySearchComponent.hide();
    });

    this._menu = $('.button-drop-menu');
    $(window).click(() => {
      this._menu.hide();
    });

    this._links = {};
    this._links[DropDownMenuComponent.LOGOUT_LINK]          = $('header .button-drop-menu [rel=logout]');
    this._links[DropDownMenuComponent.ABOUT_LINK]           = $('header .button-drop-menu [rel=about]');
    this._links[DropDownMenuComponent.HELP_LINK]            = $('header .button-drop-menu [rel=help]');
    this._links[DropDownMenuComponent.ADD_COUNTRY_LINK]     = $('header .button-drop-menu [rel=add-country]');
    this._links[DropDownMenuComponent.BUY_RESIDENTIAL_LINK] = $('header .button-drop-menu [rel=buy-residential]');

    this._links[DropDownMenuComponent.LOGOUT_LINK].on('click', () => this.onLogout());
    this._links[DropDownMenuComponent.ABOUT_LINK].on('click',  () => this.onAbout());
    this._links[DropDownMenuComponent.HELP_LINK].on('click',   () => this.onHelp());
    this._links[DropDownMenuComponent.ADD_COUNTRY_LINK].on('click',     () => this.onAddCountry());
    this._links[DropDownMenuComponent.BUY_RESIDENTIAL_LINK].on('click', () => this.onBuyResidential());

    if (ENV.MENU_HIDE_BUY_RESIDENTIAL_IP) {
      this._links[DropDownMenuComponent.BUY_RESIDENTIAL_LINK].hide();
    }
  }

  onLogout() {
    this._navigator.goTo(this._navigator.ROUTES.LOGIN_PAGE);
    this._authService.logout().then();
    this.toggleMenu();
  }

  onAbout() {
    this._navigator.goTo(this._navigator.ROUTES.ABOUT_PAGE);
  }

  onHelp() {
    chrome.tabs.create({
      url: 'http://www.geosurf.com/resources/faq'
    });
  }

  onAddCountry() {
    chrome.tabs.create({
      url: 'http://www.geosurf.com/pricing'
    });
  }

  onBuyResidential() {
    chrome.tabs.create({
      url: 'http://www.geosurf.com/pricing'
    });
  }

  hideLink(linkId) {
    this._links[linkId].hide();
  }

  showLink(linkId) {
    this._links[linkId].show();
  }

  toggleMenu() {
    this._menu.toggle();
  }
}

DropDownMenuComponent.LOGOUT_LINK          = 'LOGOUT_LINK';
DropDownMenuComponent.ABOUT_LINK           = 'ABOUT_LINK';
DropDownMenuComponent.HELP_LINK            = 'HELP_LINK';
DropDownMenuComponent.ADD_COUNTRY_LINK     = 'ADD_COUNTRY_LINK';
DropDownMenuComponent.BUY_RESIDENTIAL_LINK = 'BUY_RESIDENTIAL_LINK';

export default DropDownMenuComponent;