/**
 * Component LoginForm
 */
class LoginFormComponent {
  constructor(authService, navigator, loaderComponent, contentMessageDispatcher) {
    this._authService       = authService;
    this._navigator         = navigator;
    this._contentMessageDispatcher = contentMessageDispatcher;
    this._loaderComponent   = loaderComponent;
    this._identityElement   = $('.login-form input[name=email]');
    this._passwordElement   = $('.login-form input[name=password]');
    this._loginButton       = $('#button-login');
    this._loginSSOButton    = $('#button-login-sso');
    this._rememberMeElement = $('#login-remember');

    this._loginButton.on('click', () => {
        this.onClickLogin().then();
        // don't submit a form
        return false
      }
    );

    this._loginSSOButton.on('click', () => {
        this.onClickSSOLogin().then();
        // don't submit a form
        return false
      }
    );

    if (this._authService.getIdentity()) {
      this.setIdentity(authService.getIdentity());
    }
  }

  render() {
    if (ENV.SSO_IS_ENABLED) {
      $('#login-form__general').hide();
      $('#login-form__sso').show();
    }
    else {
      $('#login-form__sso').hide();
      $('#login-form__general').show();
    }
  }

  async onClickLogin() {
    const identity = this.getIdentity();
    const password = this.getPassword();

    if (identity.length === 0 || password.length === 0) {
      return;
    }

    this._loaderComponent.show();
    const rememberMe = this.isCheckedRememberMe();

    try {
      const response = await this._authService.login(identity, password, rememberMe);
      this._loaderComponent.hide();
      console.log(response);
      if (response.isAuthorized()) {
        this.removeErrorsMarkFromIdentityAndPassword();
        this.setPassword(null);
        $('#login-page-error').hide();
        this._navigator.goTo(this._navigator.ROUTES.MAIN_PAGE);
      }
      else {
        this.markIdentityAndPasswordHasError();
        $('#login-page-error').text('Wrong email or password.').show();
        $('div.outer-wrapper-contextmenu').hide();
      }
    }
    catch (e) {
      console.log('CATCH');
      console.log(e);
    }
  }

  async onClickSSOLogin() {
    this._contentMessageDispatcher.sendMessageToBackgroundPage({
      method: 'OPEN_NEW_TAB',
      url: ENV.SSO_IDP,
    }).then();
  }

  getIdentity() {
    return this._identityElement.val();
  }

  setIdentity(email) {
    return this._identityElement.val(email);
  }

  isCheckedRememberMe() {
    return this._rememberMeElement.prop('checked');
  }

  focusIdentity() {
    return this._identityElement.focus();
  }

  markIdentityHasError() {
    return this._identityElement.addClass(this.ERROR_CLASS_NAME);
  }

  removeErrorMarkFromIdentity() {
    return this._identityElement.removeClass(this.ERROR_CLASS_NAME);
  }

  getPassword() {
    return this._passwordElement.val();
  }

  setPassword(password) {
    return this._passwordElement.val(password);
  }

  markPasswordHasError() {
    return this._passwordElement.addClass(this.ERROR_CLASS_NAME);
  }

  focusPassword() {
    return this._passwordElement.focus();
  }

  removeErrorMarkFromPassword() {
    return this._passwordElement.removeClass(this.ERROR_CLASS_NAME);
  }

  markIdentityAndPasswordHasError() {
    this.markIdentityHasError();
    this.markPasswordHasError();
  }

  removeErrorsMarkFromIdentityAndPassword() {
    this.removeErrorMarkFromIdentity();
    this.removeErrorMarkFromPassword();
  }
}

LoginFormComponent.prototype.ERROR_CLASS_NAME = 'error';

export default LoginFormComponent;