class RotationComponent {
  constructor(locationService, settingsService, renderSystem, tabService) {
    this._locationService = locationService;
    this._settingsService = settingsService;
    this._renderSystem    = renderSystem;
    this._tabService      = tabService;
    this._el    = $('#rotation-interval');
    this._input = $('#rotation-interval input');

    this._input.on('change', () => this.onChange().then());
  }

  async onChange() {
    const map = [0, 60, 600, 1800];
    const seconds = map[this.getValue()];
    await this._locationService.setRotation(seconds);
    this._renderSystem.render();
  }

  render() {
    if (this._locationService.getLocationConfiguration().isRotationEnabled()) {
      this.makeVisible();
      if (this._locationService.hasActivatedLocation()) {
        if (this._locationService.getRotation() === 0) {
          this.setValue(0);
        }
        else if (this._locationService.getRotation() === 60) {
          this.setValue(1);
        }
        else if (this._locationService.getRotation() === 600) {
          this.setValue(2);
        }
        else if (this._locationService.getRotation() === 1800) {
          this.setValue(3);
        }

        let location = this._locationService.getActivatedLocation();

        if (this._locationService.isRotationPossible(location)) {
          this.enable();
        }
        else {
          this.disable();
        }

      }
      else {
        if (this._settingsService.isCurrentHighRotation()) {
          this.setValue(0);
        }
        else if (this._settingsService.isCurrentRotationMinute()) {
          this.setValue(1);
        }
        else if (this._settingsService.isCurrentRotation10Minutes()) {
          this.setValue(2);
        }
        else if (this._settingsService.isCurrentRotation30Minutes()) {
          this.setValue(3);
        }
        this.disable();
      }

    }
    else {
      this.disable();
      this.makeInvisible();
    }
  }

  makeVisible() {
    this._el.removeClass('invisible');
  }

  makeInvisible() {
    this._el.addClass('invisible');
  }

  setValue(value) {
    this._input.val(value);
    this.trigger('input');
  }

  getValue() {
    return parseInt(this._input.val());
  }

  trigger(event) {
    this._input.trigger(event);
  }

  disable() {
    this._el.attr('disabled', 'disabled');
    this._input.attr('disabled', 'disabled');
  }

  enable() {
    this._el.removeAttr('disabled');
    this._input.removeAttr('disabled');
  }
}

export default RotationComponent;