import DropDownMenuComponent from '../../components/DropDownMenu/DropDownMenuComponent';
import BottomMenuComponent   from '../../components/BottomMenu/BottomMenuComponent';
import ContentMessages       from '../../../js/messages/ContentMessages';


/**
 * Main Page
 */
class MainPage {
  constructor(
      authService,
      navigator,
      deviceService,
      locationCommonService,
      backgroundScript,
      cacheService,
      loaderComponent,
      dropDownMenuComponent,
      countryButtonComponent,
      changeProxyTypeButtonComponent,
      rotationComponent,
      switchIpComponent,
      citySelectionComponent,
      stateSelectionComponent,
      bottomMenuComponent,
      settingsService,
      bgCore,
  ) {
    this._authService       = authService;
    this._navigator         = navigator;
    this._deviceService     = deviceService;
    this._locationCommonService  = locationCommonService;
    this._backgroundScript  = backgroundScript;
    this._cacheService      = cacheService;
    this._loaderComponent   = loaderComponent;
    this._dropDownMenuComponent = dropDownMenuComponent;
    this._countryButtonComponent = countryButtonComponent;
    this._changeProxyTypeButtonComponent = changeProxyTypeButtonComponent;
    this._rotationComponent = rotationComponent;
    this._switchIpComponent = switchIpComponent;
    this._citySelectionComponent = citySelectionComponent;
    this._stateSelectionComponent = stateSelectionComponent;
    this._bottomMenuComponent = bottomMenuComponent;
    this._settingsService = settingsService;
    this._bgCore = bgCore;

    this._pageElement   = $('#main-page');
    $('.main-menu-item.clear').on('click', () => {
      this._loaderComponent.show('Clearing Cookies and Cache');
      const exceptionList = this._settingsService.getCookiesExceptionList();
      this._cacheService.removeCookiesAndCache(exceptionList)
      .then(
        () => {
          this._loaderComponent.hide();
          chrome.tabs.query({ currentWindow: true, active: true, url: ['http://*/*', 'https://*/*'] }, function(tabs) {
            if (tabs && tabs.length > 0) {
              chrome.tabs.sendMessage(tabs[0].id, {
                method: ContentMessages.SHOW_POPUP_COOKIES_AND_CACHE_IS_CLEAN,
              });
            }
          });
        },
      );
    });

    $('.main-menu-item.settings').on('click', () => {
      this._navigator.goTo(this._navigator.ROUTES.SETTING_PAGE);
    });
  }

  show() {
    this._pageElement.show();
  }

  render() {
    this._dropDownMenuComponent.showLink(DropDownMenuComponent.LOGOUT_LINK);

    if (this._bgCore.getService('app').isFirstLogin()) {
      this._loaderComponent.show('Loading your profile');
      this._bgCore.getService('app').waitOnDataForFirstLogin().then(
        () => {
          this.render();
        },
      );
    }
    else {
      this._loaderComponent.hide();
    }

    this._renderHeader();
    this._countryButtonComponent.render();
    this._changeProxyTypeButtonComponent.render();
    this._rotationComponent.render();
    this._switchIpComponent.render();
    this._citySelectionComponent.render();
    this._stateSelectionComponent.render();
    this._renderDevice();
    this._renderBottomButtons();
  }

  _renderBottomButtons() {
    if (this._locationCommonService.hasActivatedLocation()) {
      this._bottomMenuComponent.showButtons([
        BottomMenuComponent.TURN_OFF_BUTTON,
        BottomMenuComponent.CONTACT_US_BUTTON,
      ]);

      this._bottomMenuComponent.enableTurnOffButton();
    }
    else {
      if (this._locationCommonService.hasPrevLocations()) {
        this._bottomMenuComponent.showButtons([
          BottomMenuComponent.TURN_ON_BUTTON,
          BottomMenuComponent.CONTACT_US_BUTTON,
        ]);

        let title = 'Turn on the previous proxy: ' + this._locationCommonService.getPrevLocation().getLocation().getName().getName();
        this._bottomMenuComponent.setTitleTurnOnButton(title);
      }
      else {
        this._bottomMenuComponent.showButtons([
          BottomMenuComponent.TURN_OFF_BUTTON,
          BottomMenuComponent.CONTACT_US_BUTTON,
        ]);
        this._bottomMenuComponent.disableTurnOffButton();
      }

    }
  }

  _renderDevice() {
    if (this._deviceService.getCurrent()) {
      $('.main-menu .mobile').addClass('active');
    }
    else {
      $('.main-menu .mobile').removeClass('active');
    }
  }

  _renderHeader() {
    if (this._locationCommonService.getActivatedLocation()) {
      const countryName = this._locationCommonService.getActivatedLocation().getName().getName();
      componentMainPageHeaderSetText(countryName);
    }
    else {
      componentMainPageHeaderShowDefaultText();
    }
  }
}

function componentMainPageHeaderShowDefaultText() {
  $('.form-header .title-def').show();
  $('.form-header .title').text('').hide();
}

function componentMainPageHeaderSetText(text) {
  $('.form-header .title-def').hide();
  $('.form-header .title').text(text).show();
}

export default MainPage;