/**
 * Component ChangeProxyTypeButton
 */
class ChangeProxyTypeButtonComponent {
  constructor(
    locationCommonService,
    settingsService,
    residentialProxyService,
    renderSystem,
    showAlert,
    authService,
  ) {
    this._locationService          = locationCommonService;
    this._settingsService          = settingsService;
    this._residentialProxyService  = residentialProxyService;
    this._renderSystem = renderSystem;
    this._showAlert = showAlert;
    this._authService = authService;

    this._input = $('.proxy-type input');
    this._label = $('.proxy-type .label');
    this._input.on('change', () => {
      let checked = this._input.prop('checked');
      this._input.prop('checked', !checked);
      this.onChange()
    });
  }

  onChange() {
    try {
      if (this._locationService.isCurrentLocationHandler('dataCenter')) {
        this.onChangeResidential();
      }
      else {
        this.onChangeDataCenter();
      }
    }
    catch (error) {
      this._showAlert(error.message);
    }
  }

  onChangeResidential() {
    if (this._authService.isAuthorizedInService('residential') === false) {
      throw new Error("You have no access to residential IP service for further information please contact us");
    }

    if (this._locationService.hasActivatedLocation()) {
      this._locationService.deactivateCurrentLocation()
      .then(
        () => {
          this.setPositionToResidential();
          this._locationService.changeLocationService('residential');
          this._renderSystem.render();
        },
      );
    }
    else {
      this.setPositionToResidential();
      this._locationService.changeLocationService('residential');
      this._renderSystem.render();
    }

  }

  onChangeDataCenter() {
    if (this._authService.isAuthorizedInService('dataCenter') === false) {
      throw new Error("You have no access to Data Center IP service for further information please contact us");
    }

    if (this._locationService.hasActivatedLocation()) {
      this._locationService.deactivateCurrentLocation()
      .then(
        () => {
          this.setPositionToDataCenter();
          this._locationService.changeLocationService('dataCenter');
          this._renderSystem.render();
        },
      );
    }
    else {
      this.setPositionToDataCenter();
      this._locationService.changeLocationService('dataCenter');
      this._renderSystem.render();
    }

  }

  render() {
    this.setTextForLabel(this._locationService.getLocationConfiguration().getName());
    if (this._locationService.isCurrentLocationHandler('residential')) {
      this.setPositionToResidential();
    }
    else {
      this.setPositionToDataCenter();
    }
  }

  setPosition(type) {
    if (type === ChangeProxyTypeButtonComponent.RESIDENTIAL_TYPE) {
      this._input.prop('checked', true);
    }
    else if (type === ChangeProxyTypeButtonComponent.DATA_CENTER_TYPE) {
      this._input.prop('checked', false);
    }
    this.setTextForLabel(this._locationService.getLocationConfiguration().getName());
  }

  getPosition() {
    let checked = this._input.prop('checked');
    return checked ? ChangeProxyTypeButtonComponent.RESIDENTIAL_TYPE : ChangeProxyTypeButtonComponent.DATA_CENTER_TYPE;
  }

  setPositionToDataCenter() {
    this.setPosition(ChangeProxyTypeButtonComponent.DATA_CENTER_TYPE);
  }

  setPositionToResidential() {
    this.setPosition(ChangeProxyTypeButtonComponent.RESIDENTIAL_TYPE);
  }

  setTextForLabel(text) {
    this._label.text(text);
  }
}

ChangeProxyTypeButtonComponent.DATA_CENTER_TYPE = 'DATA_CENTER_TYPE';
ChangeProxyTypeButtonComponent.RESIDENTIAL_TYPE = 'RESIDENTIAL_TYPE';

export default ChangeProxyTypeButtonComponent;