import LoggerService           from '../js/services/Logger/LoggerService';
window._loggerService = new LoggerService();
import CountrySearchComponent         from './components/CountrySearch/CountrySearchComponent';
import StateSelectionComponent        from './components/StateSelection/StateSelectionComponent';
import CitySelectionComponent         from './components/CitySelection/CitySelectionComponent';
import LoaderComponent                from './components/Loader/LoaderComponent';
import RotationComponent              from './components/Rotation/RotationComponent';
import ChangeProxyTypeButtonComponent from './components/ChangeProxyTypeButton/ChangeProxyTypeButtonComponent';
import SettingsRotationComponent      from './components/SettingsRotation/SettingsRotationComponent';
import BottomMenuComponent            from './components/BottomMenu/BottomMenuComponent';
import RenderSystem                   from './core/RenderSystem';
import ContactUsPage                  from './pages/ContactUs/ContactUsPage';
import SettingsPage                   from './pages/Settings/SettingsPage';
import LoginFormComponent             from './components/LoginForm/LoginFormComponent';
import SwitchIpComponent              from './components/SwitchIp/SwitchIpComponent';
import ThanksPage                     from './pages/Thanks/ThanksPage';
import DropDownMenuComponent          from './components/DropDownMenu/DropDownMenuComponent';
import LoginPage                      from './pages/Login/LoginPage';
import AboutPage                      from './pages/About/AboutPage';
import CountryButtonComponent         from './components/CountryButton/CountryButtonComponent';
import NavigationSystem               from './core/NavigationSystem';
import MainPage                       from './pages/Main/MainPage';
import ContentMessageDispatcher       from '../js/content/ContentMessageDispatcher';

$(function() {

  chrome.runtime.getBackgroundPage(function (backgroundPage) {
    window.locationCommonService    = backgroundPage.locationCommonService;
    window.authService              = backgroundPage.authService;
    window.deviceService            = backgroundPage.deviceService;
    window.residentialProxyService  = backgroundPage.residentialProxyService;
    window.backgroundScript         = backgroundPage.backgroundScript;
    window.statisticsService        = backgroundPage.statisticsService;
    window.settingsService          = backgroundPage.settingsService;
    window.cacheService             = backgroundPage.cacheService;
    window.tabService               = backgroundPage.tabService;
    window.bgCore                   = backgroundPage.bgCore;

    const contentMessageDispatcher  = new ContentMessageDispatcher();

    $('.mobile-search-box input').on('keyup', function() {
      let query = $(this).val().toLowerCase();
      let $items = $('.mobile-search-wrapper .mobile-search-list > div');
      $items.show();
      if (query && query.length > 0) {
        $.each($items, (index, element) => {
          if ($(element).text().toLowerCase().search(query) < 0) {
            $(element).hide();
          }
        });
      }
    });

    $('header .header-logo').on('click', () => {
      chrome.tabs.create({
        url: 'https://www.geosurf.com/'
      });
    });

    /* Sign-up*/
    $('#button-signup').on('click', (event)=> {
      chrome.tabs.create({
        url: 'https://www.geosurf.com/products/geosurf-toolbar/'
      });
    });


    /**
     * Component Alert 'toastr'
     */
    $('.alert').on('click', hideAlert);

    function showAlert(text) {
      $('.alert')
        .addClass('in')
        .find('span')
        .text(text);

      window.alertTimeout = setTimeout(hideAlert, 5000);
    }

    function hideAlert() {
      if (window.alertTimeout) {
        clearInterval(window.alertTimeout);
        window.alertTimeout = null;
      }
      $('.alert').removeClass('in');
    }


    var rangeSlider = function() {
      var slider = $('.range-slider'),
        range = $('.range-slider__range'),
        value = $('.range-slider__value');
      slider.each(function() {
        value.each(function() {
          var value = $(this).prev().attr('value');
          $(this).html(value);
        });

        range.on('input', function() {
          let val = this.value;

          if (val == '2') {
            val = '10';
          }
          else if (val == '3') {
            val = '30';
          }
          $(this).next(value).html(val);
        });
      });
    };

    rangeSlider();

    const goToPageId = authService.isAuthorized()
      ? NavigationSystem.prototype.ROUTES.MAIN_PAGE
      : NavigationSystem.prototype.ROUTES.LOGIN_PAGE;

    let navigator = new NavigationSystem(goToPageId);
    let loaderComponent        = new LoaderComponent();
    let switchIpComponent      = new SwitchIpComponent(locationCommonService, loaderComponent, tabService);
    let loginFormComponent     = new LoginFormComponent(authService, navigator, loaderComponent, contentMessageDispatcher);

    let renderSystem = new RenderSystem(
      authService,
      navigator,
      deviceService,
      locationCommonService,
      backgroundScript
    );

    navigator.setRenderSystem(renderSystem);

    let bottomMenuComponent = new BottomMenuComponent(
      locationCommonService,
      deviceService,
      navigator,
      authService,
      loaderComponent,
      renderSystem,
      tabService,
    );

    let rotationComponent  = new RotationComponent(
      locationCommonService,
      settingsService,
      renderSystem,
      tabService
    );

    let settingsRotationComponent = new SettingsRotationComponent(
      settingsService,
      authService,
      renderSystem
    );


    let changeProxyTypeButtonComponent = new ChangeProxyTypeButtonComponent(
      locationCommonService,
      settingsService,
      residentialProxyService,
      renderSystem,
      showAlert,
      authService
    );
    let countrySearchComponent = new CountrySearchComponent(
      locationCommonService,
      settingsService,
      loaderComponent,
      renderSystem,
      showAlert,
      tabService,
      contentMessageDispatcher,
    );

    let dropDownMenuComponent  = new DropDownMenuComponent(authService, navigator, countrySearchComponent);

    let citySelectionComponent  = new CitySelectionComponent(
      locationCommonService,
      settingsService,
      loaderComponent,
      renderSystem,
      showAlert,
      tabService
    );

    let stateSelectionComponent = new StateSelectionComponent(
      locationCommonService,
      settingsService,
      citySelectionComponent,
      loaderComponent,
      renderSystem,
      showAlert,
      tabService
    );

    let countryButtonComponent = new CountryButtonComponent(locationCommonService, countrySearchComponent, citySelectionComponent);

    renderSystem.registerPage(navigator.ROUTES.MAIN_PAGE,
      new MainPage(
        authService,
        navigator,
        deviceService,
        locationCommonService,
        backgroundScript,
        cacheService,
        loaderComponent,
        dropDownMenuComponent,
        countryButtonComponent,
        changeProxyTypeButtonComponent,
        rotationComponent,
        switchIpComponent,
        citySelectionComponent,
        stateSelectionComponent,
        bottomMenuComponent,
        settingsService,
        window.bgCore,
      )
    );

    renderSystem.registerPage(navigator.ROUTES.ABOUT_PAGE, new AboutPage(bottomMenuComponent));
    renderSystem.registerPage(navigator.ROUTES.THANKS_PAGE, new ThanksPage());
    renderSystem.registerPage(navigator.ROUTES.CONTACT_US_PAGE, new ContactUsPage(authService, bottomMenuComponent));
    renderSystem.registerPage(navigator.ROUTES.SETTING_PAGE,
      new SettingsPage(
        authService,
        settingsService,
        settingsRotationComponent,
        bottomMenuComponent,
        showAlert
      )
    );

    renderSystem.registerPage(navigator.ROUTES.LOGIN_PAGE,
      new LoginPage(
        bottomMenuComponent,
        dropDownMenuComponent,
        loginFormComponent
      )
    );

    renderSystem.render();

  });
});