import hideOnClickOutside from '../../../js/utils/hideOnClickOutside';

class StateSelectionComponent {
  constructor(
    locationCommonService,
    settingsService,
    citySelectionComponent,
    loaderComponent,
    renderSystem,
    showAlert,
    tabService
  )
  {
    this._locationService = locationCommonService;
    this._settingsService = settingsService;
    this._citySelectionComponent = citySelectionComponent;
    this._loaderComponent = loaderComponent;
    this._renderSystem    = renderSystem;
    this._showAlert       = showAlert;
    this._tabService      = tabService;

    this._checkboxWrapper   = $('.wrapper-state-checkbox');
    this._list              = $('.state-search-wrapper');
    this._stateCheckboxText = $('.stateCheckboxText');
    this._checkbox          = $('#stateCheckbox');
    this._checkbox.on('change', () => this.onClickCheckBox());

    $('.state-search-box input').on('keyup', function() {
      let query = $(this).val().toLowerCase();
      let $items = $('.state-search-wrapper .state-search-list > div');
      $items.show();
      if (query && query.length > 0) {
        $.each($items, (index, element) => {
          if ($(element).text().toLowerCase().indexOf(query) < 0) {
            $(element).hide();
          }
        });
      }
    });

    hideOnClickOutside(this._list);

  }

  onClickCheckBox() {
    let list = [];
    if (!this._locationService.hasActivatedLocation()) {
      this._tabService.reloadCurrentTab().then();
    }

    let location = this._locationService.getActivatedLocation();

    if (location.getAdminUnit() === 'COUNTRY') {
      list = location.getChildren();
    }
    else {
      let country = location.getParent();
      list = country.getChildren();
      this._loaderComponent.show();
      this._locationService.activateLocation(country)
      .then(
        () => {
          this.setList(list);
          this._list.show();
          this._renderSystem.render();
        },
        (error) => {
          this._showAlert(error);
          this._loaderComponent.hide();
        }
      );
      return;
    }

    $('.state-search-box input').val('');
    this.setList(list);
    this._list.show();
    this.render();
  }

  async onClickItem(location) {
    this._list.hide();

    if (this._locationService.getLocationConfiguration().isRotationEnabled()) {
      await this._locationService.setRotation(this._settingsService.getRotation());
    }
    this._citySelectionComponent.setList(location.getChildren());
    this._loaderComponent.show();

    try {
      await this._locationService.activateLocation(location);
      this._loaderComponent.hide();
      this._renderSystem.render();
      await this._tabService.reloadCurrentTab();
    }
    catch (e) {
      this._showAlert(e);
      this._loaderComponent.hide();
    }
  }

  render() {
    if (
      this._locationService.hasActivatedLocation() &&
      this._locationService.getActivatedLocation().isChildrenHasAdminUnitStates()
    ) {
      this._checkbox.prop('checked', false);
      this.setStateText('Select a State');
      this._checkboxWrapper.show();
    }
    else if (this._locationService.hasActivatedLocation() && this._locationService.getActivatedLocation().getAdminUnit() === 'STATE') {
      this._checkbox.prop('checked', true);
      this.setStateText(this._locationService.getActivatedLocation().getName().getName());
      this._checkboxWrapper.show();
    }
    else if (
      this._locationService.hasActivatedLocation() &&
      this._locationService.getActivatedLocation().getAdminUnit() !== 'COUNTRY' &&
      this._locationService.getActivatedLocation().getParent().getAdminUnit() === 'STATE'
  ) {
      this._checkbox.prop('checked', true);
      let stateLocation = this._locationService.getActivatedLocation().getParent();
      let list = stateLocation.getChildren();
      this.setStateText(stateLocation.getName().getName());
      this._checkboxWrapper.show();
    }
    else {
      this._checkboxWrapper.hide();
      this._list.hide();
    }
  }

  removeList() {
    let $list = this._list.find('.state-search-list');
    $list.empty();
  }

  setList(locations) {
    this.removeList();
    for (let key in locations) {
      if (locations.hasOwnProperty(key)) {
        let location = locations[key];
        let isChecked = this._locationService.hasActivatedLocation() ? this._locationService.getActivatedLocation().getCode().getCode() === location.getCode().getCode() : false;
        let element = this._creteProxyItemElement(location.getName().getName(), isChecked);
        this.addProxy(element, location);
      }
    }
  }

  addProxy(element, location) {
    let $list = this._list.find('.state-search-list');
    element.on('click', () => this.onClickItem(location).then() );
    $list.append(element);
  }

  setStateText(text) {
    this._stateCheckboxText.text(text);
  }

  _creteProxyItemElement(name, isChecked) {
    let className = isChecked ? 'fa-check' : '';
    let check = '<i class="fa ' + className + '" aria-hidden="true"></i>';
    let str = `
          <div class="item">
              ${check} <span>${name}</span>
          </div>
      `;

    return $(str);
  }

}


export default StateSelectionComponent;