/**
 * Component CountryButton
 */

function stateSearchWrapperHide() {
  $('.state-search-wrapper').hide();
}

class CountryButtonComponent {
  constructor(locationCommonService, countrySearchComponent, citySelectionComponent) {
    this._locationService = locationCommonService;
    this._countrySearchComponent = countrySearchComponent;
    this._citySelectionComponent = citySelectionComponent;

    this._button = $('.select-country-button');
    this._button.on('click', () => this.onClick());
  }

  onClick() {
    this._countrySearchComponent.removeList();

    stateSearchWrapperHide();
    this._citySelectionComponent.hideList();

    setTimeout(() => {
      this._countrySearchComponent.setFilter('');
      this._countrySearchComponent.focusOnFilterElement();
    }, 50);

    if (this._countrySearchComponent.isShown()) {
      let locations = this._locationService.getLocations();
      this._countrySearchComponent.setList(locations);
    }

    this._countrySearchComponent.toggle();
  }

  setDefaultFlag() {
    $('.select-country-button .proxy-flag').addClass('flag-icon-default');
    $('.select-country-button').removeClass('active');
  }

  removeCurrentFlag() {
    $('.select-country-button .proxy-flag').attr(
        'class',
        $('.select-country-button .proxy-flag').attr('class').replace(/\bflag-icon-\w+(-\w+)?/g, '')
    );
  }

  setFlagByCode(countryCode) {
    let flag = $('.proxy-flag.flag-icon');
    flag.addClass('flag-icon-' + countryCode.toLowerCase());
    flag.removeClass('flag-icon-default');
    $('.select-country-button').addClass('active');
  }

  render() {
    this.removeCurrentFlag();
    if (this._locationService.hasActivatedLocation()) {
      let countryCode = this._locationService.getActivatedLocation().getCountryCode().getCode();
      console.log('SET FLAG: ' + countryCode);
      this.setFlagByCode(countryCode);
    }
    else {
      console.log('setDefaultFlag');
      this.setDefaultFlag();
    }
  }
}

export default CountryButtonComponent;