import hideOnClickOutside from '../../../js/utils/hideOnClickOutside';

class CitySelectionComponent {
  constructor(locationCommonService, settingsService, loaderComponent, renderSystem, showAlert, tabService) {
    this._locationService = locationCommonService;
    this._settingsService = settingsService;
    this._loaderComponent = loaderComponent;
    this._renderSystem    = renderSystem;
    this._tabService      = tabService;
    this._showAlert       = showAlert;

    this._list             = $('.city-search-wrapper');
    this._checkboxWrapper  = $('.wrapper-city-checkbox');
    this._checkbox         = $('#cityCheckbox');
    this._cityCheckboxText = $('.cityCheckboxText');

    this._checkbox.on('change', () => this.onClickCheckBox());
    hideOnClickOutside(this._list);
    $('.city-search-box input').on('keyup', function() {
      let query = $(this).val().toLowerCase();
      let $items = $('.city-search-wrapper .city-search-list > div');
      $items.show();
      if (query && query.length > 0) {
        $.each($items, (index, element) => {
          if ($(element).text().toLowerCase().indexOf(query) < 0) {
            $(element).hide();
          }
        });
      }
    });
  }

  onClickCheckBox() {
    if (
      this._locationService.hasActivatedLocation() &&
      !this._locationService.getActivatedLocation().isChildrenHasAdminUnitCities()
    ) {
      this._tabService.reloadCurrentTab().then();
    }

    const location = this._locationService.getActivatedLocation();
    let list = [];

    if (location.getAdminUnit() === 'COUNTRY' || location.getAdminUnit() === 'STATE') {
      list = location.getChildren();
    }
    else if (location.getAdminUnit() === 'CITY') {
      let parent  = this._locationService.getActivatedLocation().getParent();
      list = parent.getChildren();
      this._locationService.activateLocation(parent)
        .then(
          () => {
            this.setList(list);
            this._list.show();
            this._renderSystem.render();
          },
          (error) => {
            this._showAlert(error);
            this._loaderComponent.hide();
          }
        );
      return;
    }
    else {
      list = this._locationService.getChildren();
    }

    this.setList(list);
    this.showList();
    this.render();
  }

  async onClickItem(location) {
    this.hideList();
    if (this._locationService.getLocationConfiguration().isRotationEnabled()) {
      await this._locationService.setRotation(this._settingsService.getRotation());
    }
    this._loaderComponent.show();
    try {
      await this._locationService.activateLocation(location);
      this._loaderComponent.hide();
      this._renderSystem.render();
      await this._tabService.reloadCurrentTab();
    }
    catch (e) {
      this._showAlert(e);
      this._loaderComponent.hide();
    }

  }

  render() {
    if (
      this._locationService.hasActivatedLocation() &&
      this._locationService.getActivatedLocation().isChildrenHasAdminUnitCities()
    ) {
      this._checkbox.prop('checked', false);
      this.setCityText('Select a City');
      this.showCheckBox();
    }
    else if (
      this._locationService.hasActivatedLocation() &&
      this._locationService.getActivatedLocation().getAdminUnit() === 'CITY'
    ) {
      this._checkbox.prop('checked', true);
      this.setCityText(this._locationService.getActivatedLocation().getName().getName());
      this.showCheckBox();
    }
    else {
      this.hideCheckBox();
      this.hideList();
    }
  }

  removeList() {
    let $list = this._list.find('.city-search-list');
    $list.empty();
  }

  showCheckBox() {
    this._checkboxWrapper.show();
  }

  hideCheckBox() {
    this._checkboxWrapper.hide();
  }

  showList() {
    $('.city-search-box input').val('');
    this._list.show();
  }

  hideList() {
    this._list.hide();
  }

  setList(locations) {
    this.removeList();
    for (let key in locations) {
      if (locations.hasOwnProperty(key)) {
        let location = locations[key];
        let isChecked = this._locationService.hasActivatedLocation()
                        ? this._locationService.getActivatedLocation().getCode().getCode() === location.getCode().getCode()
                        : false;
        let element = this._creteProxyItemElement(location.getName().getName(), isChecked);
        this.addProxy(element, location);
      }
    }
  }

  addProxy(element, location) {
    let $list = this._list.find('.city-search-list');
    element.on('click', () => this.onClickItem(location).then() );
    $list.append(element);
  }

  setCityText(text) {
    this._cityCheckboxText.text(text);
  }

  _creteProxyItemElement(name, isChecked) {
    let className = isChecked ? 'fa-check' : '';
    let check = '<i class="fa ' + className + '" aria-hidden="true"></i>';
    let str = `
          <div class="item">
              ${check} <span>${name}</span>
          </div>
      `;

    return $(str);
  }

}

export default CitySelectionComponent;