/**
 * Component Navigator
 */
class NavigationSystem {
  constructor(pageId, renderSystem) {
    this._currentPage = null;
    this.goTo(pageId, false);
  }

  setRenderSystem(renderSystem) {
    this._renderSystem = renderSystem;
  }

  goTo(pageId, needRender) {
    this._currentPage = pageId;
    if (needRender !== false) {
      this._renderSystem.render();
    }
  }

  whereAmI() {
    return this._currentPage;
  }

  amIonPage(pageId) {
    return this.whereAmI() === pageId;
  }
}

NavigationSystem.prototype.ROUTES = {
  LOGIN_PAGE:      'login-page',
  MAIN_PAGE:       'main-page',
  SETTING_PAGE:    'settings-page',
  ABOUT_PAGE:      'about-page',
  THANKS_PAGE:     'thanks-page',
  CONTACT_US_PAGE: 'contact-us-page',
};

export default NavigationSystem;