import BottomMenuComponent from '../../components/BottomMenu/BottomMenuComponent';
import DropDownMenuComponent from '../../components/DropDownMenu/DropDownMenuComponent';

/**
 * Login Page
 */
class LoginPage {
  constructor(bottomMenuComponent, dropDownMenuComponent, loginFormComponent) {
    this._bottomMenuComponent   = bottomMenuComponent;
    this._dropDownMenuComponent = dropDownMenuComponent;
    this._loginFormComponent    = loginFormComponent;
    this._pageElement = $('#login-page');
  }

  show() {
    this._pageElement.show();
  }

  render() {
    this._bottomMenuComponent.showButtons([
      BottomMenuComponent.CONTACT_US_BUTTON,
      BottomMenuComponent.SETTENING_BUTTON,
    ]);

    this._loginFormComponent.render();

    this._dropDownMenuComponent.hideLink(DropDownMenuComponent.LOGOUT_LINK);

    if (this._loginFormComponent.getIdentity()) {
      this._loginFormComponent.focusPassword();
    }
    else {
      this._loginFormComponent.focusIdentity();
    }
  }
}

export default LoginPage;