import BottomMenuComponent from '../../components/BottomMenu/BottomMenuComponent';

class ContactUsPage {
  constructor(authService, bottomMenuComponent) {
    this._authService   = authService;
    this._bottomMenuComponent = bottomMenuComponent;
    this._el = $('#contact-us-page');

    this._emailEl      = this._el.find('input[name=email]');
    this._departmentEl = document.querySelector('#contact-us-page select[name="department"]');
    this._messageEl    = this._el.find('textarea[name=message]');
    this._subjectEl    = this._el.find('input[name=subject]');
  }

  show() {
    this._el.css('display', 'inline-block');
    this._departmentEl.selectedIndex = 0;
    this._subjectEl.val('');
    this._messageEl.val('');
  }

  render() {
    this._bottomMenuComponent.showButtons([
      BottomMenuComponent.CANCEL_BUTTON,
      BottomMenuComponent.SUBMIT_BUTTON,
    ]);

    $('#contact-us-page-error').css('visibility', 'hidden').text('');

    if (this._authService.getIdentity()) {
      this._emailEl.val(this._authService.getIdentity());
    }
  }
}

export default ContactUsPage;