/**
 * Render Section
 */
class RenderSystem {
  constructor(authService, navigator, deviceService, proxyService) {
    this._authService   = authService;
    this._navigator     = navigator;
    this._deviceService = deviceService;
    this._locationService  = locationCommonService;

    this._pages = {};
  }

  registerPage(routeId, page) {
    this._pages[routeId] = page;
  }

  render() {
    const routeId = this._navigator.whereAmI();
    this._renderPage(routeId);
  }

  _renderPage(pageId) {
    this._hideAllPages();
    this._pages[pageId].show();
    this._pages[pageId].render();
  }

  _hideAllPages() {
    $('section.content > div').hide();
  }
}

export default RenderSystem;